/**
 * Base,  el-radioel-radio-group 
 * @module $ui/components/my-radio
 */
import {Radio, RadioGroup, RadioButton} from 'element-ui'
import Base from '../Base'

/**
 * 
 * @slots
 * @property {string} loading 
 * @property {string} option  item index
 */

export default {
  name: 'MyRadio',
  mixins: [Base],
  components: {
    Radio,
    RadioGroup,
    RadioButton
  },
  /**
   *  
   *  @member props
   *  @property {*} [value] 
   *  @property {boolean} [button] group=true 
   *  @property {boolean} [group] 
   *  @property {string} [optionWidth]  grouptrue buttonfalse
   *  @property {string} [loadingText=...] , 
   */
  props: {
    value: {
      type: [Array, String, Number, Boolean]
    },
    
    // 
    button: Boolean,
    
    // 
    group: {
      type: Boolean,
      default: true
    },
    
    //  grouptrue buttonfalse
    optionWidth: String,
    
    // 
    loadingText: {
      type: String,
      default: '...'
    }
  },
  computed: {
    optionStyle() {
      return {
        width: this.optionWidth
      }
    },
    classes() {
      return {
        'my-radio': true,
        'has-label-width': ('lable-width' in this.$attrs) || (this.myForm && 'label-width' in this.myForm.$attrs)
      }
    }
  },
  render() {
    let vnode = null
    if (this.group) {
      const scopedSlots = (opt, index) => {
        return this.$scopedSlots.option
          ? this.$scopedSlots.option({
            item: opt,
            index: index
          })
          : opt[label]
      }
      // checkbox  options  label  value
      // el-checkbox trueLable falseLable disabled name checked
      const {label, value} = this.keyMap
      const items = this.button
        ? this.currentOptions.map((opt, index) => {
          const props = {...opt, label: opt[value]}
          return (
            <RadioButton {...{props: props}}>
              {scopedSlots(opt, index)}
            </RadioButton>
          )
        })
        : this.currentOptions.map((opt, index) => {
          const props = {...opt, label: opt[value]}
          return (
            <Radio style={this.optionStyle} {...{props: props}}>
              {scopedSlots(opt, index)}
            </Radio>
          )
        })
      
      vnode = this.loading
        ? (
          <div class="my-form__loading my-radio__loading">
            {this.$slots.loading || this.loadingText}
          </div>
        )
        : (
          <RadioGroup ref="comp"
                      class={this.classes}
                      {...{props: this.innerProps, on: this.$listeners}}
                      vModel={this.fieldValue}>
            {items}
          </RadioGroup>
        )
      
    } else {
      vnode = (
        <Radio ref="comp"
               class={this.classes}
               {...{props: this.innerProps, on: this.$listeners}}
               vModel={this.fieldValue}></Radio>
      )
    }
    
    return this.renderComponent(vnode)
  }
}
