
  ---
title: 
---
<template>
  <my-wrapper title="" fit>
    <my-container class="data-stat" :border="false">
      <el-row>
        <el-col :span="8">
          <span class="title"></span>
          <span class="info">8</span>
        </el-col>
        <el-col :span="8">
          <span class="title"></span>
          <span class="info">32</span>
        </el-col>
        <el-col :span="8">
          <span class="title"></span>
          <span class="info">24</span>
        </el-col>
      </el-row>
    </my-container>
    <my-container :border="false">
      <my-table ref="table"
                toolbar
                stripe
                title=""
                :columns="columns"
                :loader="loader"
                :show-header="false">
        <template v-slot:actions>
          <my-form class="filter-form"
                   :model="query"
                   @change="handleFilterChange"
                   inline
                   :footer="null"
                   size="small">
            <my-radio name="state"
                      button
                      :options="[{label:'',value:1},{label:'',value:2},{label:'',value:3}]"></my-radio>
            <my-input name="keyword" placeholder="" :props="{prefixIcon:'el-icon-search', clearable:true}">
            </my-input>
          </my-form>
        </template>

        <template v-slot:prepend>
          <div class="add-btn" @click="handleAdd"><i class="el-icon-plus"></i></div>
        </template>

        <template v-slot:name="{row}">
          <div class="data-item">
            <my-avatar :size="50" shape="square">{{row.name.substring(0,1)}}</my-avatar>
            <h4>{{row.name}}</h4>
            <p>{{row.title}}</p>
          </div>
        </template>
        <template v-slot:county="{row}">
           <br> {{row.county}}
        </template>
        <template v-slot:created="{row}">
           <br> {{row.created}}
        </template>
        <template v-slot:age="{row}">
          <el-progress :percentage="row.age"></el-progress>
        </template>
        <template v-slot:handle="{row}">
          <el-button type="text" @click="handleEdit(row)"></el-button>
          <el-divider direction="vertical"></el-divider>
          <el-button type="text" @click="handleRemove(row)"></el-button>
        </template>

      </my-table>
    </my-container>
    <my-dialog :title="dialogTitle"
               :footer="null"
               target="body"
               modal
               width="50%"
               :visible.sync="visible">
      <my-form size="medium"
               label-width="100px"
               :on-submit="handleSubmit"
               :rules="rules"
               :model="form">
        <my-input label="" name="title"></my-input>
        <my-date-picker label="" name="created"
                        :props="{type:'datetime',valueFormat:'yyyy-MM-dd hh:mm:ss'}"></my-date-picker>
        <my-select label="" name="name" :options="[{label:'', value:1}]"></my-select>
        <my-input label="" name="info" :props="{type:'textarea', rows:5}"></my-input>
      </my-form>
    </my-dialog>
  </my-wrapper>
</template>

<script>
  
     import __vue__ from 'vue';
     import {Button as __Button__,Col as __Col__,Divider as __Divider__,Progress as __Progress__,Row as __Row__} from 'element-ui';
     import {MyAvatar as __MyAvatar__,MyContainer as __MyContainer__,MyDatePicker as __MyDatePicker__,MyDialog as __MyDialog__,MyForm as __MyForm__,MyInput as __MyInput__,MyRadio as __MyRadio__,MySelect as __MySelect__,MyTable as __MyTable__,MyWrapper as __MyWrapper__} from '$ui';
     
     
     
     import MockForExample from '$my/code/mixin/mock-for-example'

  export default {
    mixins: [MockForExample],
    data() {
      return {
        query: {
          state: 1,
          keyword: ''
        },
        columns: [
          {label: '', prop: 'name', minWidth: 200},
          {label: '', prop: 'county', minWidth: 150},
          {label: '', prop: 'created'},
          {label: '', prop: 'age'},
          {label: '', prop: 'handle', width: 100}
        ],
        form: {},
        rules: {
          title: {required: true}
        },
        visible: false,
        isEditModel: false
      }
    },
    computed: {
      dialogTitle() {
        return this.isEditModel ? '' : ''
      }
    },
    methods: {
      handleFilterChange(model) {
        clearTimeout(this.refreshTimer)
        this.refreshTimer = setTimeout(() => {
          this.$refs.table.refresh(1)
        }, 300)
      },
      loader(page, limit) {
        return this.fetchMockForExample({
          data: {
            ...this.query,
            page,
            limit
          }
        })
      },
      handleAdd() {
        this.form = {}
        this.isEditModel = false
        this.visible = true
      },
      handleEdit(row) {
        this.form = row
        this.isEditModel = true
        this.visible = true
      },
      handleRemove(row) {
        this.$confirm('?', '', {type: 'warning'})
          .then(ok => {
            this.$message.success('')
            this.removeMockForExample({
              id: row.id
            }).then(r => {
              this.$refs.table.refresh()
            })
          })
          .catch(e => e)
      },
      handleSubmit(model) {
        const handler = this.isEditModel
          ? this.updateMockForExample({
            data: model
          })
          : this.addUser({
            data: model
          })
        return handler.then(res => {
          this.visible = false
          this.$refs.table.refresh(this.isEditModel ? null : 1)
          return res
        }).catch(e => e)
      }
    },
    beforeDestroy() {
      clearTimeout(this.refreshTimer)
    }
  }
     __vue__.use(__Button__);
__vue__.use(__Col__);
__vue__.use(__Divider__);
__vue__.use(__Progress__);
__vue__.use(__Row__)
     __vue__.use(__MyAvatar__);
__vue__.use(__MyContainer__);
__vue__.use(__MyDatePicker__);
__vue__.use(__MyDialog__);
__vue__.use(__MyForm__);
__vue__.use(__MyInput__);
__vue__.use(__MyRadio__);
__vue__.use(__MySelect__);
__vue__.use(__MyTable__);
__vue__.use(__MyWrapper__)
    
  </script>


<style lang="scss" scoped>
  @import "~@/style/_vars.scss";

  .data-stat {
    .el-col {
      text-align: center;
      border-left: 1px solid $--color-divider;

      &:first-child {
        border-left: none;
      }
    }

    .title {
      color: $--color-placeholder;
      display: block;
      margin-bottom: 8px;
    }

    .info {
      color: $--color-primary-text;
      display: block;
      font-size: $--font-size-large;
      padding: 5px;
    }
  }

  .filter-form {
    /deep/ {
      .el-form-item {
        margin-bottom: 0 !important;
      }

      .my-radio {
        transform: translateY(-2px);
      }
    }
  }

  .add-btn {
    border: 1px dashed $--color-divider;
    padding: 5px;
    text-align: center;
    margin: 14px 0;
    cursor: pointer;

    i {
      margin-right: 10px;
    }

    &:hover {
      border-color: $--link-hover-color;
      color: $--color-primary;
    }
  }

  .data-item {
    .my-avatar {
      float: left;
    }

    h4, p {
      margin: 0;
      padding: 0 0 0 60px;
    }

    h4 {
      color: $--color-primary-text;
    }
  }
</style>

  